function revaluate(value, ticker, timestamp) {
    if ((ticker === 'SBER') && (timestamp < 1183233600000)) {
        //    console.log(timestamp, ticker, value / 1000);
        return value / 1000
    }
    if ((ticker === 'SBERP') && (timestamp < 1183233600000)) {
        //    console.log(timestamp, ticker, value / 1000);
        return value / 20
    }
    return value
}


function normalizeQuotes(series) {
    console.log(series);

    let indexData = [...series[0].data];
    let assetData = series[1].data.map(item => [item[0], revaluate(item[1], series[1].name, item[0])]);

    let normalizedIndexArray = [];
    let normalizedAssetArray = [];


    if (series[0].data[0][0] > series[1].data[0][0]) {
  //      console.log('     ');

        let IndexMinimalDatePrice = indexData[0][1];
        //  console.log(IndexMinimalDatePrice);

        let IndexMinimalDate = indexData[0][0];
        //  console.log(IndexMinimalDate);

        let AssetMinimalDatePrice = 1;
        try {
            AssetMinimalDatePrice = assetData.find(item => item[0] === IndexMinimalDate)[1]
            //    console.log(AssetMinimalDatePrice)
        } catch (error) {
            console.error(error);
        }

        indexData.map(item => {
            let findElementAssetThisDate = assetData.find(assetItem => assetItem[0] === item[0]);
            //  console.log(findElementAssetThisDate);
            if (!!findElementAssetThisDate) {
                let assetValueInThisDate = findElementAssetThisDate[1];
                normalizedIndexArray.push([item[0], parseInt(item[1] * 100 / IndexMinimalDatePrice)]);
                //    console.log('Index data', [item[0], parseInt(item[1] * 100 / IndexMinimalDatePrice)])
                normalizedAssetArray.push([item[0], parseInt(assetValueInThisDate * 100 / AssetMinimalDatePrice)]);
                //    console.log('Asset data', [item[0], parseInt(assetValueInThisDate * 100 / AssetMinimalDatePrice)])
            }
        })

    } else {
 //       console.log('    ');

        let AssetMinimalDatePrice = assetData[0][1];
   //     console.log(AssetMinimalDatePrice);

        let AssetMinimalDate = assetData[0][0];
   //     console.log(AssetMinimalDate);

        let IndexMinimalDatePrice = 1;
        try {
            IndexMinimalDatePrice = indexData.find(item => item[0] === AssetMinimalDate)[1]
     //       console.log(AssetMinimalDatePrice)
        } catch (error) {
     //       console.error(error);
        }

        assetData.map(item => {
            let findElementIndexThisDate = indexData.find(indexItem => indexItem[0] === item[0]);
       //     console.log(findElementIndexThisDate);
            if (!!findElementIndexThisDate) {
                let indexValueInThisDate = findElementIndexThisDate[1];
                normalizedIndexArray.push([item[0], parseInt(indexValueInThisDate * 100 / IndexMinimalDatePrice)]);
         //       console.log('Index data', [item[0], parseInt(indexValueInThisDate * 100 / IndexMinimalDatePrice)])
                normalizedAssetArray.push([item[0], parseInt(item[1] * 100 / AssetMinimalDatePrice)]);
         //       console.log('Asset data', [item[0], parseInt(item[1] * 100 / AssetMinimalDatePrice)])
            }
        })


    }

 //   console.log(normalizedIndexArray);
 //   console.log(normalizedAssetArray);

    let scatterdata = normalizedAssetArray.map((item, index) => {
        if (index > 0) {
   //         let scatterDate = item[0];
            let indexReturn = Math.round((normalizedIndexArray[index][1] / normalizedIndexArray[index - 1][1] - 1) * 100 * 10) / 10;
            let assetReturn = Math.round((normalizedAssetArray[index][1] / normalizedAssetArray[index - 1][1] - 1) * 100 * 10) / 10;;
            { return [indexReturn, assetReturn] }
        } else { return [0, 0] }
    });
    //  console.log(scatterdata);

    let new_series = [{ name: series[0].name, data: [...normalizedIndexArray] },
    { name: series[1].name, data: [...normalizedAssetArray] }
    ];

    return { series: new_series, scatterdata: scatterdata }
}

function StudentScatterChartBetaCompany(props) {
    //   const data = useSelector(selectUserData).data;
    const data = useSelector(selectTempData).dataArray;
    const [selectedAsset, setSelectedAsset] = useState(null);
    const [selectedIndex, setSelectedIndex] = useState(null);


    const [lineChartData, setLineChartData] = useState(null);
    const [scatterChartData, setScatterChartData] = useState(null);

    useEffect(() => {
        if (!!selectedAsset && !!selectedIndex) {
            let assetTickerArray = selectedAsset.split("_");
            let indexTickerArray = selectedIndex.split("_");
            if (assetTickerArray[1] === indexTickerArray[1]) {
                let datesIndexArrayRedux = data.filter(item => item.datatype === 'quote' && item.ticker === indexTickerArray[0] && item.per === indexTickerArray[1])
                    .map(quoteItem => {
                        let year = quoteItem.quotedate.substring(0, 4);
                        let month = quoteItem.quotedate.substring(4, 6);
                        let day = quoteItem.quotedate.substring(6, 8);
                        let date = + new Date(year, month - 1, day);
                        //        console.log(date);
                        return [date, parseFloat(quoteItem.close)]
                    });
                let datesAssetArrayRedux = data.filter(item => item.datatype === 'quote' && item.ticker === assetTickerArray[0] && item.per === assetTickerArray[1])
                    .map(quoteItem => {
                        let year = quoteItem.quotedate.substring(0, 4);
                        let month = quoteItem.quotedate.substring(4, 6);
                        let day = quoteItem.quotedate.substring(6, 8);
                        let date = + new Date(year, month - 1, day);
                        //        console.log(date);
                        return [date, parseFloat(quoteItem.close)]
                    });

                let series = [
                    {
                        name: indexTickerArray[0],
                        data: [...datesIndexArrayRedux]
                    },
                    {
                        name: assetTickerArray[0],
                        data: [...datesAssetArrayRedux]
                    }
                ];

                let normalizeQuotesData = normalizeQuotes(series);
                setLineChartData(normalizeQuotesData.series);
                setScatterChartData(normalizeQuotesData.scatterdata);
            }
        }

    }, [selectedAsset, selectedIndex]);



    if (!!lineChartData && !!scatterChartData && !!data && data.length > 0) {
        return <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
            <Card.Header></Card.Header>
            <Card.Body>
                <Card.Title> Beta </Card.Title>
                <ImportFinamQuotesCSVSpreadsheet openButton={" CSV     "} />
                <SelectAssetAndIndex setSelectedAsset={setSelectedAsset} setSelectedIndex={setSelectedIndex} />
                <DatalineQuotesApexChartOptions series={lineChartData} /> {/*  */}
                <ScatterQuotesApexChartOptions series={scatterChartData} /> {/*  */}
                <CheckAnswer scatterChartData={scatterChartData} lineChartData={lineChartData} />
            </Card.Body>


        </Card>
    }

    return <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
        <Card.Header></Card.Header>
        <Card.Body>
            <Card.Title> Beta </Card.Title>
            <Card.Text>         finam.ru</Card.Text>
            <ImportFinamQuotesCSVSpreadsheet openButton={" CSV     "} />
            {!!data && data.length > 0 ? <SelectAssetAndIndex setSelectedAsset={setSelectedAsset} setSelectedIndex={setSelectedIndex} /> : null}
        </Card.Body>
    </Card>
}

export default StudentScatterChartBetaCompany;

function CheckAnswer(props) {
    const [value, setValue] = useState("");
    const [answerIsRight, setAnswerIsRight] = useState(null);
    const dispatch = useDispatch();
    const email = useSelector(selectApplication).email;
    const user = useSelector(selectApplication).user;
    const avatarUrl = useSelector(selectApplication)?.avatarUrl;


    //  function handleChange(event) { setValue(event.target.value); }

    function handleCheckAnswer() {
        let res = regressionGradient(props.scatterChartData);
        if (
            parseFloat(value) / parseFloat(res) < 1.05 &&
            parseFloat(value) / parseFloat(res) > 0.95
        ) {
            setAnswerIsRight(true);

            let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
            let idData = firebase.database().ref(userEmail).child('data').push().key;
            let idPost = firebase.database().ref(userEmail).child('posts').push().key;

            let postObject = {
                id: idPost, //Math.floor(Math.random() * 1001),
                title: 'Beta ', //props.title,
                theme: 'Beta ', // props.theme,
                answer: res,
                comment: 'Beta ', // props.title + " (" + props.theme + ")", //
                type: "media",
                content: idData,
                quizString: ' Beta       ', //props.text,
                deleted: false,
                email: email,
                user: user,
                avatarUrl: !!avatarUrl ? avatarUrl : '',
                date: new Intl.DateTimeFormat("ru", {
                    weekday: "short",
                    year: "numeric",
                    month: "short",
                    day: "numeric",
                    hour: "numeric",
                    minute: "numeric",
                }).format(new Date()), //Date().toJSON()
            };


            let dataObject = {
                datalinequotesapexchartoptions: {
                    series: props.lineChartData,
                    user: user,
                    avatarUrl: avatarUrl
                },
                scatterquotesapexchartoptions: {
                    series: props.scatterChartData,
                    user: user,
                    avatarUrl: avatarUrl
                }
            }

            console.log(dataObject);

            var updates = {};
            updates['/usersCraft/' + userEmail + '/posts/' + idPost] = postObject;
            // updates['/currentDay/' + currentDay + '/posts/' + idPost] = currentDayObject;
            updates['/usersCraft/' + userEmail + '/data/' + idData] = dataObject;
            return firebase.database().ref().update(updates);
        } else { dispatch(emptyTempData()) }
    }


    return <><InputGroup size="sm" style={{ width: "95%", margin: "1rem" }}>
        <InputGroup.Prepend>
            <Button variant="outline-secondary" onClick={handleCheckAnswer}></Button>
            <FormControl
                value={value}
                type="number"
                onChange={(event) => setValue(event.target.value)}
                aria-label="Answer"
                aria-describedby="inputGroup-answer"
            />
        </InputGroup.Prepend>

    </InputGroup>
        {answerIsRight ? <Alert variant={'success'}>
            <ShowRegressionString arrayOfarrays={props.scatterChartData} />
        </Alert> : null}

    </>
}

function SelectAssetAndIndex(props) {
    //  const data = useSelector(selectUserData).data;
    const data = useSelector(selectTempData).dataArray;
    const [selectedAsset, setSelectedAsset] = useState(null);
    const [selectedIndex, setSelectedIndex] = useState(null);
    //   console.log(data);

    let assetList = [];
    data.forEach(element => {
        let hasCurrentElement = assetList.some(item => item.ticker === element.ticker && item.per === element.per);
        if (!hasCurrentElement) { assetList.push({ ticker: element.ticker, per: element.per }) }
    });
    //   console.log(assetList);

    function doSelectAsset(e) { setSelectedAsset(e); props.setSelectedAsset(e) }

    function doSelectIndex(e) { setSelectedIndex(e); props.setSelectedIndex(e) }

    return <div>
        <InputGroup className="m-2">
            <FormControl
                placeholder={selectedAsset ? selectedAsset : " "}
                aria-label="asset"
                aria-describedby="basic-asset"
            />

            <DropdownButton
                as={InputGroup.Append}
                variant="outline-secondary"
                title=""
                id="asset-group-dropdown"
            >
                {assetList.map(item =>
                    <Dropdown.Item key={item.ticker + "_" + item.per} eventKey={item.ticker + "_" + item.per} onSelect={(e) => doSelectAsset(e)}>{item.ticker + "_" + item.per}</Dropdown.Item>
                )}
                {/* <Dropdown.Item href="#">Action</Dropdown.Item>
                <Dropdown.Divider />
                <Dropdown.Item href="#">Separated link</Dropdown.Item> */}
            </DropdownButton>
        </InputGroup>

        <InputGroup className="m-2">
            <FormControl
                placeholder={selectedIndex ? selectedIndex : " - "}
                aria-label="asset"
                aria-describedby="basic-asset"
            />

            <DropdownButton
                as={InputGroup.Append}
                variant="outline-secondary"
                title=""
                id="index-group-dropdown"
            >
                {assetList.map(item =>
                    <Dropdown.Item key={item.ticker + "_" + item.per} eventKey={item.ticker + "_" + item.per} onSelect={(e) => doSelectIndex(e)}>{item.ticker + "_" + item.per}</Dropdown.Item>
                )}
            </DropdownButton>
        </InputGroup>

    </div>
}
